import { MIN_PRICE, MAX_PRICE } from '../const';

/**
 *        min  max
 * @param {Number} min     
 * @param {Number} max     
 * @returns      min  max
 */
const getRandomInteger = (min = MIN_PRICE, max = MAX_PRICE) => Math.floor(Math.random() * (max - min + 1)) + min;

/**
 *     
 * @param {Array} items ,      
 * @returns {any}   
 */
const getRandomArrayElement = (items) => items[getRandomInteger(0, items.length - 1)];

/**
 *      
 * @param {string} word ,      
 * @returns {string}        ,        null/undefined
 */
const capitalizeFirstLetter = (word) => {
  if (!word) {
    return '';
  }
  return word.charAt(0).toUpperCase() + word.slice(1);
};

/**
 *      Escape
 * @param {Event} event -  keydown
 * @returns {boolean} -  true,     Escape
 */
const isEscapeKey = (event) => event.key === 'Escape';

/**
 * ""   
 * @param {Array} items   
 * @param {event} update   
 * @returns {Array}    
 */
function updateItem(items, update) {
  return items.map((item) => item.id === update.id ? update : item);
}

export { getRandomInteger, getRandomArrayElement, capitalizeFirstLetter, isEscapeKey, updateItem };
