import { filter } from '../utils/filter';

/**
 *            
 * @param {Array} events  
 * @return {Array}          
 * @example
 * generateFilter(events);
 */
function generateFilter (events) {
  return Object.entries(filter).map(
    ([filterType, filterEvents]) => ({
      type: filterType,
      count: filterEvents(events).length,
    }),
  );
}

export { generateFilter };
